@import "mixins";
@import "fonts";
@import "variables";

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  color: $colorLightest;
  background-color: $colorHighlight;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

a {
  text-decoration: none;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}

.swiper-container {
  height: 100vh;
  transition-duration: 0.6s;
  transition-delay: 0.25s;
}

.swiper-slide {
  overflow: hidden;
  opacity: 0;
  transition-duration: 1s;

  &-active {
    opacity: 1;
  }
}

.slide-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transition-timing-function: cubic-bezier(1,.01,.35,1.01);
}

.copy {
  position: absolute;
  bottom: 15px;
  left: 0;
  right: 0;
  text-align: center;
  color: white;
  letter-spacing: 0.06em;
}

.swiper-light {
  background-color: $colorLightest;
}

@media (min-width: $adaptiveMax) {
  .swiper-container {
    min-height: 100vh;
  }

  .swiper-slide {
    overflow: hidden;
    opacity: 0;
    transition-duration: 1s;

    &-active {
      opacity: 1;
    }
  }

  .slide-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
    transition-timing-function: ease-in;
  }

  .copy {
    position: absolute;
    bottom: 15px;
    left: 0;
    right: 0;
    text-align: center;
    color: white;
    letter-spacing: 0.06em;
  }
}
