@import "styles/animations/abstractMove";
@import "styles/animations/abstractScale";
@import "styles/animations/animateAbstractOpacity1";
@import "styles/variables";

@keyframes revealItem {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.shapeAbstract :global {
  animation-name: abstractRotate;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-duration: 180s;
}

.shapes {
  opacity: 0;
  transition-duration: $transitionDuration;
}

.shapesVisible {
  opacity: 1;
}

.shapeWrapper {
  opacity: 0;
  transition-duration: $transitionDuration;
}

.shape :global {
  animation-name: abstractMove, opacity, abstractScale;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.shapeWrapperVisible1 {
  animation-name: revealItem;
  animation-delay: 6s;
  animation-duration: $transitionDuration;
  animation-fill-mode: forwards;
}

.shape1 {
  animation-duration: 12s;
  animation-delay: 6s, 0s, 0s;
  offset-path: path("M-48.30000000000001,-20a200,30 0 1,0 400,0a200,30 0 1,0 -400,0");
}

